import React, { Component } from "react";
import { Button, Input } from "reactstrap";
import "./screenShotStyles.css";
import StylePage, { StyledButton } from "app/common/component/StylePage";
import { AvForm } from "availity-reactstrap-validation";
import { Header } from "app/common/component/Header";
import "../../App.css";
import { createIssue, addAttachment } from "../../utils/issueUtils";
import createScreenShotWindow from "../../createWindow";
const fs = window.require("fs");
// import image from '../../../tempImage/image.png'
class CreateIssueContainer extends Component {
  imageRef = React.createRef();
  state = {
    severity: "",
    issueType: "",
    summary: "",
    desc: "",
    imageSrc: "",
    isSubmit: false,
    ticketID: "",
    isAttachment: false
  };
  componentDidMount() {
    window.addEventListener(
      "visibilitychange",
      () => {
        const path = "./tempImage/image.png";

        try {
          if (fs.existsSync(path)) {
            this.setState({
              isAttachment: true
            });
          } else {
            this.setState({
              isAttachment: false
            });
          }
        } catch (err) {}
      },
      false
    );
  }
  componentWillUnmount() {
    window.removeEventListener("visibilitychange", () => {});
  }
  handleSubmit = () => {
    // this.setState({
    //   isAttachment: true
    // });
    createScreenShotWindow();
  };

  showScreenshotScreen = () => {
    return (
      <div>
        <Button onClick={() => this.props.history.push("/ReportIssue")}>
          backButton
        </Button>
        <Button
          onClick={e => {
            this.props.history.push("/ReportFeedback");
          }}
          id="submit"
        >
          Report Issue
        </Button>
      </div>
    );
  };

  onSubmitIssue = img => {
    const { severity, issueType, summary, desc, imgSrc } = this.state;
    if (img === imgSrc) this.setState({ imgSrc: imgSrc });
    this.setState({ isSubmit: true });
    if (severity !== "" && issueType !== "" && summary !== "" && desc !== "") {
      createIssue(
        this.props.history.projectDetails,
        { summary, desc, severity, issueType },
        issueDetails => {
          // this.setState({
          //   ticketID: issueDetails.key
          // });
          this.state.isAttachment && addAttachment(issueDetails.key);
          Object.assign(this.props.history, {
            issueDetails: issueDetails
          });
          this.props.history.push("/ReportFeedback");
        }
      );
    }
  };

  showSummaryPage = () => {
    const { severityArray } = this.props.history.projectDetails;
    const {
      severity,
      issueType,
      summary,
      desc,
      isSubmit,
      isAttachment
    } = this.state;
    return (
      <StylePage Header={<Header h2={"Creating Issue"} />}>
        <AvForm>
          <div style={{ fontSize: "10px" }}>
            {summary === "" && isSubmit && (
              <span className="issue-alert">Please enter summary</span>
            )}
            <Input
              type="text"
              name="text"
              id="exampleText"
              placeholder="Summary"
              onChange={e =>
                this.setState({
                  summary: e.target.value
                })
              }
            />
            {desc === "" && isSubmit && (
              <span className="issue-alert">Please select description</span>
            )}
            <Input
              type="textarea"
              placeholder="Description"
              name="text"
              id="exampleTextAria"
              onChange={e =>
                this.setState({
                  desc: e.target.value
                })
              }
            />
            <div className="optionWrapper">
              <div style={{ width: "100%", marginRight: "5px" }}>
                {severity === "" && isSubmit && (
                  <span className="issue-alert">Please select description</span>
                )}
                <select
                  className="drop-report-issue"
                  onChange={e => this.setState({ severity: e.target.value })}
                >
                  <option value="" disabled selected hidden>
                    Severity
                  </option>
                  {severityArray &&
                    severityArray.map((val, index) => (
                      <option id={index} key={index}>
                        {val}
                      </option>
                    ))}
                </select>
              </div>
              <div style={{ width: "100%", marginLeft: "5px" }}>
                {issueType === "" && isSubmit && (
                  <span className="issue-alert">Please select description</span>
                )}
                <select
                  className="drop-report-issue"
                  onChange={e => this.setState({ issueType: e.target.value })}
                >
                  <option value="" disabled selected hidden>
                    Issue Type
                  </option>
                  {["Bug", "Improvement"].map((val, index) => (
                    <option key={index} id={index}>
                      {val}
                    </option>
                  ))}
                </select>
              </div>
            </div>
          </div>
          {isAttachment && (
            <div
              style={{
                backgroundColor: "#f5f5f5",
                border: "1px solid transparent",
                fontWeight: "normal",
                width: "100%",
                marginBottom: "5px",
                textAlign: "left",
                color: "grey"
              }}
            >
              <span> {"image.png"}</span>
              <button
                style={{
                  float: "right",
                  border: "0",
                  background: "border-box"
                }}
                onClick={() => {
                  const path = "./tempImage/image.png";

                  fs.unlink(path, err => {
                    if (err) {
                      return;
                    }

                    //file removed
                  });
                  this.setState({
                    isAttachment: false
                  });
                }}
              >
                <i
                  style={{ color: "palevioletred" }}
                  class="glyphicon glyphicon-trash"
                ></i>
              </button>
            </div>
          )}
          <div
            style={{
              display: "flex",
              justifyContent: "center",
              marginTop: "30px"
            }}
          >
            <StyledButton
              style={{ marginRight: "20px" }}
              name=" Take Snapshot"
              id="submit"
              type="submit"
              onClick={this.handleSubmit}
            />
            <StyledButton
              style={{ marginLeft: "20px" }}
              name="Create Issue"
              id="submit"
              type="submit"
              onClick={this.onSubmitIssue}
            />
          </div>
        </AvForm>
        <div
          style={{
            textDecoration: "underline",
            color: "blue",
            fontSize: "12px",
            marginTop: "23px",
            float: "left",
            cursor: "pointer"
          }}
          onClick={() => {
            this.props.history.push("/ReportIssue");
          }}
        >
          {" "}
          {"  <- Back to Project Info"}
        </div>
      </StylePage>
    );
  };

  render() {
    return this.showSummaryPage();
  }
}

export default CreateIssueContainer;
