import React, { Component } from "react";
import "../../App.css";
import {
  getProjectComponents,
  getProjectVersions,
  getProjectCustomFields
} from "../../utils/issueUtils";
import _ from "lodash";
import StylePage, { StyledButton } from "app/common/component/StylePage";
import { AvForm } from "availity-reactstrap-validation";
import { customFields } from "../../utils/customFields";
import Select from "react-select";

export default class ReportIssue extends Component {
  constructor(props) {
    super(props);

    this.state = {
      projectName: "",
      components: [],
      projectPhase: "",
      projectRound: "",
      platforms: [],
      foundDuring: "",
      projectTester: "",
      category: "",
      isSubmit: false
    };
    this.project = this.props.history.projectList;
  }

  resetOptions = projectName => {
    _.find(this.project.projects, pt => {
      if (pt.name === projectName) {
        getProjectComponents(pt.id, projectComponent => {
          this.components = projectComponent.map(val => val.name);
          this.detailedComponentArray = projectComponent;
          this.setState({
            projectName: pt.key,
            components: []
          });
        });
        getProjectVersions(pt.id, versionsArr => {
          this.phases = versionsArr.map(val => val.name);
        });
        getProjectCustomFields(pt.key, fields => {
          console.log(fields, "fields");
          this.rounds = this.getDropDownValues(fields[customFields.rounds]);
          this.platforms = this.getDropDownValues(
            fields[customFields.platforms]
          );
          this.found_during = this.getDropDownValues(
            fields[customFields.found_during]
          );
          this.testers = this.getDropDownValues(fields[customFields.testers]);
          this.category = this.getDropDownValues(fields[customFields.category]);
          this.severity = this.getDropDownValues(fields[customFields.severity]);
        });
      }
    });
  };

  getDropDownValues = field =>
    field.allowedValues && field.allowedValues.map(val => val.value);

  handleProjectChange = event => {
    console.log("sangharsh");
    this.resetOptions(event.target.value);
  };

  handleSubmit = e => {
    const {
      projectName,
      components,
      projectPhase,
      projectRound,
      platforms,
      foundDuring,
      projectTester,
      category
    } = this.state;
    e.preventDefault();
    this.setState({
      isSubmit: true
    });
    if (
      projectName !== "" &&
      components !== [] &&
      projectPhase !== "" &&
      projectRound !== "" &&
      platforms !== [] &&
      foundDuring !== "" &&
      projectTester !== "" &&
      category !== ""
    ) {
      Object.assign(this.props.history, {
        projectDetails: {
          key: projectName,
          components: components,
          phase: projectPhase,
          round: projectRound,
          platforms: platforms,
          foundDuring: foundDuring,
          tester: projectTester,
          category: category,
          severityArray: this.severity
        }
      });

      this.props.history.push("/CreateIssue");
    }
  };

  IndicatorSeparator = ({ innerProps }) => {
    const indicatorSeparatorStyle = {
      alignSelf: "stretch",
      backgroundColor: "#5243AA",
      marginBottom: 8,
      marginTop: 8,
      width: 1
    };
    return <span style={indicatorSeparatorStyle} {...innerProps} />;
  };

  renderMultiSelectDropDown = (options, placeholder, stateVar) => {
    console.log("options", options);
    const formattedOptions = options.map(option => {
      return { value: option, label: option };
    });
    return (
      <Select
        closeMenuOnSelect={false}
        components={this.IndicatorSeparator}
        isMulti
        options={formattedOptions}
        placeholder={placeholder}
        onChange={e => {
          this.setState({
            [stateVar]: e && e.map(val => val.value)
          });
        }}
      />
    );
  };

  renderReportPageIntro = () => {
    const {
      projectName,
      components,
      projectPhase,
      projectRound,
      platforms,
      foundDuring,
      projectTester,
      category,
      isSubmit
    } = this.state;
    return (
      <div className="projectFormWrapper">
        {projectName === "" && isSubmit && (
          <span className="issue-alert">Please select project</span>
        )}
        <select
          onChange={this.handleProjectChange}
          className="drop-report-issue"
        >
          <option value="" disabled selected hidden>
            Select Project
          </option>
          {this.project.projects &&
            this.project.projects.map((val, index) => (
              <option>{val.name}</option>
            ))}
        </select>
        {components && components.length === 0 && isSubmit && (
          <span className="issue-alert">Please select component/s</span>
        )}
        {this.renderMultiSelectDropDown(
          this.components,
          "Select Component/s",
          "components"
        )}
        <div style={{ display: "flex", width: "100%" }}>
          <div style={{ width: "50%", marginRight: "5px" }}>
            {projectRound === "" && isSubmit && (
              <span className="issue-alert">Please select round</span>
            )}
            <select
              className="drop-report-issue"
              onChange={e => this.setState({ projectRound: e.target.value })}
            >
              <option value="" disabled selected hidden>
                Round
              </option>
              {this.rounds && this.rounds.map(val => <option>{val}</option>)}
            </select>
          </div>
          <div style={{ width: "50%", marginLeft: "5px" }}>
            {projectPhase === "" && isSubmit && (
              <span className="issue-alert">Please select phase</span>
            )}
            <select
              className="drop-report-issue"
              onChange={e => this.setState({ projectPhase: e.target.value })}
            >
              <option value="" disabled selected hidden>
                Phase
              </option>
              {this.phases && this.phases.map(val => <option>{val}</option>)}
            </select>
          </div>
        </div>
        {category === "" && isSubmit && (
          <span className="issue-alert">Please select category</span>
        )}
        <select
          className="drop-report-issue"
          onChange={e => this.setState({ category: e.target.value })}
        >
          <option value="" disabled selected hidden>
            Category
          </option>
          {this.category && this.category.map(val => <option>{val}</option>)}
        </select>
        {platforms && platforms.length === 0 && isSubmit && (
          <span className="issue-alert">Please select platform/s</span>
        )}
        {this.renderMultiSelectDropDown(
          this.platforms,
          "Select Platform/s",
          "platforms"
        )}
        {foundDuring === "" && isSubmit && (
          <span className="issue-alert">Found During field is empty!</span>
        )}
        <select
          className="drop-report-issue"
          onChange={e => this.setState({ foundDuring: e.target.value })}
        >
          <option value="" disabled selected hidden>
            Found During
          </option>
          {this.found_during &&
            this.found_during.map(val => <option>{val}</option>)}
        </select>
        {projectTester === "" && isSubmit && (
          <span className="issue-alert">Please select tester</span>
        )}
        <select
          className="drop-report-issue"
          onChange={e => this.setState({ projectTester: e.target.value })}
        >
          <option value="" disabled selected hidden>
            Tester Name
          </option>
          {this.testers && this.testers.map(val => <option>{val}</option>)}
        </select>

        <StyledButton
          onClick={this.handleSubmit}
          name="Set Environment"
          style={{ margin: "10px 0 0 0" }}
        />
      </div>
    );
  };
  detailedComponentArray;
  phases;
  components = [];
  rounds;
  platforms = [];
  found_during;
  testers;
  category;
  severity;

  render() {
    console.log(this.platforms, this.state, "Vinod");
    return (
      <StylePage subHeading="Project Selection" outerBoxPadding={"10px"}>
        <AvForm>{this.renderReportPageIntro()}</AvForm>
      </StylePage>
    );
  }
}
